.ChatDescription{
    width: 55%;
    min-height: 100%;
    overflow-y: auto;
    
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;

    background-color: var(--main-color);
    background-image: url("../../assets/pattern3.png");
    background-repeat: repeat;
    box-shadow: -2px 0px 5px rgba(0,0,0,.5);
}

.ChatDescription header{
    width: 100%;
    height: 25%;
    padding: 15px;
    position: sticky;
    top: 0px;

    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;

    /* background: linear-gradient(to bottom, var(--darker-color) 60%, var(--main-color)); */
    background-color: var(--transparent-darker-color);
    box-shadow: 0px 0px 10px rgba(0,0,0,.5);
    color: white;
}
.ChatDescription header h3{
    font-size: 5rem;
    font-family: 'Teko', sans-serif;
}

.ChatDescription section{
    width: 95%;
    max-width: 500px;
    padding: 20px 0px;

    color: white;
}
.ChatDescription section p{
    font-size: 1.4rem;
}

@media(max-width: 719px){
    .ChatDescription{
        width: 50%;
        scroll-snap-align: center;
    }
}