.ChatInput{
    flex: 0 0 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.ChatInput input{
    flex-grow: 1;
    height: 100%;
    min-width: 200px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    border: none;
    border-top: 1px solid grey;
    padding: 0 5px;

    font-size: 1.2rem;
    caret-color: var(--violet-color);
}
.ChatInput button{
    flex: 0 0 100px;
    height: 100%;
    background-color: var(--main-color);
    color: white;
    font-size: 1.2rem;
    border: none;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: background .3s;
    cursor: pointer;
}
.ChatInput button:hover,
.ChatInput button:active{
    background-color: var(--darker-color);
}