.ChatInterface{
    width: 45%;
    height: 100%;
    overflow-y: auto;
    background-color: #fdfdfd;
    display: flex;
    flex-direction: column;
}

.DialogBox{
    height: 100%;
    flex: 1;

    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;

    padding: 15px;
    box-sizing: border-box;
}

@media(max-width: 719px){
    .ChatInterface{
        width: 50%;
        scroll-snap-align: center;
    }
}