@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Teko&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
  font-family: 'Open Sans', sans-serif;
}

:root{
  --main-color: #6495ed;
  --transparent-darker-color: #4F5BD699;
  --darker-color: #4F5BD6;
  --violet-color: #785BF7;
}
html{
  height: 100%;
  font-size: 16px;
}
body{
  height: 100%;
}
#root{
  height: 100%;
}
.App_App__wVoBD {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  overflow: auto;
  background-color: cornflowerblue;

  scroll-behavior: smooth;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
}
.App_ChatContainer__229st{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
}

.App_ButtonNavigator__2Nai6{
  display: none;
}

@media(max-width: 719px){
  .App_ButtonNavigator__2Nai6{
    display: flex;
    flex-flow: row nowrap;
    width: 200vw;
    height: 50px;

    /* box-shadow: 0 0px 5px rgba(0,0,0,.5); */
    z-index: 5;
    cursor: pointer;
  }
  .App_ButtonNavigator__2Nai6 button{
    width: 50%;
    height: 100%;
    background-color: var(--darker-color);
    border: 1px solid white;
    border-bottom: 1px solid white;


    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .App_ButtonNavigator__2Nai6 button:first-child{
    border-right: 1px solid white;
    background-color: var(--main-color);
  }
  .App_ButtonNavigator__2Nai6 button p,
  .App_ButtonNavigator__2Nai6 button svg{
    color: white;
    font-size: 1.6rem;
    font-family: 'Open Sans', sans-serif;
    margin: 0px 10px;
  }

  .App_App__wVoBD{
    min-height: unset;
    height: 100%;
  }
  .App_ChatContainer__229st{
    height: calc(100% - 50px);
    width: 200vw;
    flex-direction: row-reverse;
  }
}

.ChatInterface_ChatInterface__2Ib4f{
    width: 45%;
    height: 100%;
    overflow-y: auto;
    background-color: #fdfdfd;
    display: flex;
    flex-direction: column;
}

.ChatInterface_DialogBox__gil__{
    height: 100%;
    flex: 1 1;

    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;

    padding: 15px;
    box-sizing: border-box;
}

@media(max-width: 719px){
    .ChatInterface_ChatInterface__2Ib4f{
        width: 50%;
        scroll-snap-align: center;
    }
}
.DialogueBubble_BubbleContainer__dzpdl{
    width: 100%;
    padding: 5px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.DialogueBubble_DialogueBubble__G735e{
    position: relative;
    max-width: 80%;
    font-size: 1rem;
    padding: 5px 15px;
    border-radius: 5px;
    box-sizing: border-box;
}
.DialogueBubble_UserBubble__xKh6F{
    align-self: flex-end;
    background-color: var(--main-color);
    color: white;
    border-top-right-radius: 0px;
}
.DialogueBubble_UserBubble__xKh6F::before{
    content: "";
    display: inline-block;
    position: absolute;
    top: 0px;
    left: 100%;
    border-top: 5px solid var(--main-color);
    border-bottom: 5px solid transparent;
    border-right: 5px solid transparent;
    border-left: 5px solid var(--main-color);
    border-top-right-radius: 3px;
}
.DialogueBubble_ChatbotBubble__2ZcS9{
    align-self: flex-start;
    border: 1px solid var(--main-color);
    color: var(--main-color);
    border-top-left-radius: 0;
}
.DialogueBubble_ChatbotBubble__2ZcS9::before{
    content: "";
    display: inline-block;
    position: absolute;
    top: -1px;
    right: 100%;
    border-top: 5px solid var(--main-color);
    border-bottom: 5px solid transparent;
    border-right: 5px solid var(--main-color);
    border-left: 5px solid transparent;
    border-top-left-radius: 3px;
}
.ChatInput_ChatInput__3KrGO{
    flex: 0 0 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.ChatInput_ChatInput__3KrGO input{
    flex-grow: 1;
    height: 100%;
    min-width: 200px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    border: none;
    border-top: 1px solid grey;
    padding: 0 5px;

    font-size: 1.2rem;
    caret-color: var(--violet-color);
}
.ChatInput_ChatInput__3KrGO button{
    flex: 0 0 100px;
    height: 100%;
    background-color: var(--main-color);
    color: white;
    font-size: 1.2rem;
    border: none;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: background .3s;
    cursor: pointer;
}
.ChatInput_ChatInput__3KrGO button:hover,
.ChatInput_ChatInput__3KrGO button:active{
    background-color: var(--darker-color);
}
.ChatDescription_ChatDescription__377KO{
    width: 55%;
    min-height: 100%;
    overflow-y: auto;
    
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
