    background-color: var(--main-color);
    background-image: url(data:image/png;base64,);
    background-repeat: repeat;
    box-shadow: -2px 0px 5px rgba(0,0,0,.5);
}

.ChatDescription_ChatDescription__377KO header{
    width: 100%;
